.list{
  background-color: lightseagreen;
  max-height: 720px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 30px;
  text-align: center;
  margin-top: 4%;
  position: fixed;
  right: 0;
  .barItem{
    width: 50px;
    text-decoration: none;
    margin-bottom: 10px;
    p{
      font-weight: bold;
      font-size: 12px;
      color: black;
    }
    img{
      width: 100%;
      border-radius: 50%;


    }
  }
}